@import url(https://fonts.googleapis.com/css?family=Vollkorn&display=swap);
body {
  font-family: 'Vollkorn', serif;
}
body {
  margin: 0px;
  width: 100vw;
  height: 100vh;
}

#root {
  height: 100%;
}

.map {
  height: 100%;
}

.login-form {
  z-index: 999;
  position: absolute;
  top: 5px;
  right: 5px;
  height: 400px;
  width: 22.5rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.login-form:hover {
  box-shadow: 1px 1px 2px black, 0 0 5px teal, 0 0 3px teal;
}

.login-form-failed {
  z-index: 999;
  position: absolute;
  top: 5px;
  right: 5px;
  height: 460px;
  width: 16.5rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.login-text {
  font-size: 18px;
}

.login-margin {
  margin-right: 5px;
}

.signup-form {
  z-index: 2000;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 100px;
  width: 90rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.signout {
  z-index: 999;
  position: absolute;
  top: 5px;
  right: 5px;
  height: 40px;
  width: 13.5rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.chatroom {
  z-index: 999;
  position: absolute;
  top: 5px;
  right: 150px;
  height: 40px;
  width: 13.5rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.alert-button {
  text-align: center;
  right: 2px;
  margin-top: 10px;
  width: 12rem;
  height: 50px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.error-text {
  font-size: 24px;
  color: red;
  background-color: white;
}

.profile-card {
  word-wrap: break-word;
}

.popup-style {
  background-color: black;
  text-align: center;
  font-size: 15px;
}

.leaflet-popup-content-wrapper {
  text-align: center;
}

.leaflet-popup-content {
  color: grey;
  font-size: 13px;
}

.bio-lead {
  font-style: bold;
  color: black;
  font-size: 15px;
  font-weight: v300;
  text-decoration: underline;
}

.blockquote-footer {
  margin-top: 14px;
} 

.loading-style {
  z-index: 2000;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  height: 7.5rem;
  width: 7.5rem;
  top: 300px;
}


html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}


ul {
  list-style: none;
}

h4 {
  padding-left: 20px;
  margin-bottom: 10px;
}


.Chat-Style {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
}

.Chat {
  height: unset;
}


.right-sidebar {
  border-left: 1px solid #ccc;
}

.left-sidebar {
  border-right: 1px solid #ccc;
}

.user-profile {
  height: 70px;
  display: flex;
  align-items: flex-start;
  padding-right: 20px;
  padding-left: 20px;
  justify-content: center;
  flex-direction: column;
  border-bottom: 1px solid #ccc;
}

.user-profile span {
  display: block;
}

.user-profile .username {
  font-size: 20px;
  font-weight: 700;
}

.chat-rooms li, .room-member {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  font-size: 18px;
  color: #181919;
  cursor: pointer;
  border-bottom: 1px solid #eee;
  margin-bottom: 0;
}

.room-member {
  justify-content: space-between;
  padding: 0 20px;
  height: 60px;
}

.send-dm {
  opacity: 0;
  pointer-events: none;
  font-size: 20px;
  border: 1px solid #eee;
  border-radius: 5px;
  margin-bottom: 0;
  padding: 0 10px;
  line-height: 1.4;
  height: auto;
}

.room-member:hover .send-dm {
  opacity: 1;
  pointer-events: all;
}

.presence {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #ccc;
  margin-right: 10px;
  border-radius: 50%;
}

.presence.online {
  background-color: green;
}

.chat-rooms .active {
  background-color: #eee;
  color: #181919;
}

.chat-rooms li:hover {
  background-color: #D8D1D1;
}

.room-icon {
  display: inline-block;
  margin-right: 10px;
}

.chat-screen {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.chat-header {
  height: 70px;
  flex-shrink: 0;
  border-bottom: 1px solid #ccc;
  padding-left: 10px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.chat-header h3 {
  margin-bottom: 0;
  text-align: center;
}

.chat-messages {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 0;
  min-height: -webkit-min-content;
  min-height: -moz-min-content;
  min-height: min-content;
}

.message {
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.message span {
  display: block;
  text-align: left;
}

.message .user-id {
  font-weight: bold;
}

.message-form {
  border-top: 1px solid #ccc;
}

.message-form, .message-input {
  width: 100%;
  margin-bottom: 0;
}

input[type="text"].message-input {
  height: 50px;
  border: none;
  padding-left: 20px;
}

/* Dialog
   ========================================================================== */

.dialog-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content:center;
  align-items: center;
}

.dialog {
  width: 500px;
  background-color: white;
  display: flex;
  align-items:  center;
}

.dialog-form {
  width: 100%;
  margin-bottom: 0;
  padding: 20px;
}

.dialog-form > * {
  display: block;
}

.username-label {
  text-align: left;
  font-size: 16px;
}

.username-input {
  width: 100%;
}

input[type="text"]:focus {
  border-color: #5C8436;
}

.submit-btn {
  color: #5C8436;
  background-color: #181919;
  width: 100%;
}

.submit-btn:hover {
  color: #5C8436;
  background-color: #222;
}
